@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400italic,700);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300i,400,400i,600,700,700i);
:root {
  --media-viewmode-x-small: 190px;
  --media-viewmode-small: 290px;
  --media-viewmode-medium: 428px;
  --media-viewmode-large: 930px;
}

@viewport {
  width: device-width;
  zoom: 1.1;
  min-zoom: 0.4;
  max-zoom: 2;
  user-zoom: fixed;
}
:root {
  --serif: "Libre Baskerville", serif;
  --sans-serif: "Source Sans Pro", sans-serif;
  --black: #000000;
  --white: #ffffff;
  --gray: #6c7373;
  --light-gray: #f1f1f1;
  --lightest-gray: #e4e4e4;
  --dark-gray: #2b2b2b;
  --darkest-gray: #231f20;
  --feed-gray: #393939;
  --light-blue: #67c8c7;
  --dark-blue: #00a5a4;
  --darker-blue: #005f85;
  --blue: #007a7a;
  --turquoise: #2b8282;
  --red: #d01c29;
  --dark-red: #a51417;
  --gold: #f5bd18;
  --kern-sm: 0.025em;
  --kern-md: 0.075em;
  --kern-lg: 0.16em;
  --gutter: 1.25rem;
  --desktop-width-gutter: 3rem;
  --xsm: 0.325rem;
  --sm: 0.625rem;
  --sm-md: 0.9rem;
  --md: 1.05rem;
  --lg: 1.875rem;
  --xlg: 2.5rem;
  --xxlg: 3.15rem;
  --font-weight-bold: 700;
  --font-weight-medium-bold: 600;
  --font-weight-normal: 400;
  --warning: #f5bd18;
  --warning-light: lighten(#f5bd18, 45%);
  --danger: #d01c29;
  --danger-light: lighten(#d01c29, 45%);
  --info: #00558c;
  --info-light: lighten(#00558c, 70%);
  --success: #00664f;
  --success-light: lighten(#00664f, 76%);
  /* Gin admin styles for modals  - DOT-224 */
  --gin-bg-layer: #fff;
  --gin-bg-layer2: #edeff5;
  --gin-border-m: 0.5rem;
  --gin-spacing-1: 1.5rem;
  --gin-spacing-m: 1rem;
  --gin-spacing-l: 1.5rem;
  --gin-spacing-xl: 2rem;
  --gin-font-weight-normal: 400;
  --gin-icon-size-close: 20px;
}

.tabs {
  background: var(--light-gray);
  font-size: 16px;
}
.tabs p {
  margin: 5px 0;
}
.tabs .columns.wysiwig a {
  color: var(--dark-red);
  font-weight: 600;
  text-decoration: underline;
}
.tabs .columns.wysiwig a:hover {
  color: var(--red);
}
.tabs #primary,
.tabs #secondary {
  padding: 60px 0 40px;
  background: var(--light-gray);
}
.tabs #primary.white,
.tabs #secondary.white {
  background: var(--white);
}
.tabs #primary .container,
.tabs #secondary .container {
  max-width: 1100px;
}
.tabs #primary .column,
.tabs #secondary .column {
  max-width: 900px;
  margin: 0 auto;
}
.tabs #primary h2,
.tabs #secondary h2 {
  text-align: center;
  color: var(--dark-red);
  max-width: 800px;
  margin: 0 0 60px 0;
}
.tabs #primary h2 em,
.tabs #secondary h2 em {
  font-style: normal;
  color: var(--red);
}
.tabs #primary h3,
.tabs #secondary h3 {
  color: var(--dark-red);
}
.tabs #primary h3.caps,
.tabs #secondary h3.caps {
  color: var(--dark-gray);
}
@media all and (min-width: 768px) {
  .tabs #primary,
  .tabs #secondary {
    padding: 100px 0;
  }
  .tabs #primary .columns,
  .tabs #secondary .columns {
    column-count: 2;
    column-gap: 50px;
  }
  .tabs #primary h2,
  .tabs #secondary h2 {
    margin: 0 auto 50px;
  }
}
@media all and (min-width: 1030px) {
  .tabs #primary .columns {
    -webkit-column-gap: 100px;
    -moz-column-gap: 100px;
    column-gap: 100px;
  }
  .tabs #primary .columns.wysiwig {
    font-size: 1rem;
  }
  .tabs #primary .columns p {
    margin: 1rem 0;
  }
  .tabs p {
    margin: 1rem 0;
  }
}
.tabs header {
  background: var(--white);
  text-align: center;
  padding: 70px 0 50px;
}
.tabs header h2 {
  width: 85%;
  margin: 0 auto;
  color: var(--dark-red);
}

.tab-nav {
  background: var(--white);
  box-sizing: border-box;
}
.tab-nav .container {
  max-width: 1006px;
}
.tab-nav .tab-heading {
  width: 25%;
  box-sizing: border-box;
  border-right: 1px solid var(--white);
}
.tab-nav .tab-heading:last-child {
  border-right: none;
}

.tab-heading {
  cursor: pointer;
  position: relative;
  color: var(--white);
  font-family: var(--sans-serif);
  text-align: center;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 0.075em;
  margin-bottom: 0;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
}
.tab-heading:hover .inner {
  background: var(--red);
}
.tab-heading .inner {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  background: #949494;
  padding: 18px 12px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.tab-heading.active .inner {
  background: var(--red);
}

.tab-content {
  position: relative;
  background: var(--white);
  max-width: 1140px;
  margin: 0 auto;
}

.tab {
  position: relative;
  padding-top: 40px;
}
.tab .tab-inner {
  display: none;
}
.tab .legend {
  margin: 0 auto;
  max-width: 725px;
  font-weight: 600;
  padding: 0 7.5%;
}
.tab .webform-submission-contact-add-form {
  padding: 60px 7.5% 0;
}
.tab .tab-heading {
  border-bottom: 1px solid var(--white);
}

.tab.active .tab-inner {
  display: block;
  position: relative;
  visibility: visible;
  opacity: 1;
}
.tab.active .tab-heading .inner {
  background: var(--red);
}

@media all and (min-width: 600px) {
  .tab .tab-heading {
    display: none;
  }
  .tab .tab-inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: 0.2s;
    transition: 0.2s;
  }
}
@media all and (min-width: 1030px) {
  .tabs {
    padding-bottom: 90px;
  }
  .contact .tabs {
    margin-top: -40px;
  }
}
@media all and (max-width: 599px) {
  .tab-nav {
    display: none;
  }
}
@media all and (min-width: 768px) {
  .tab-nav .tab-heading {
    padding: 0 8px;
    border: none;
  }
}
.tab {
  overflow: hidden;
  z-index: 3;
  padding-top: unset;
}

.tab button {
  background-color: inherit;
  float: right;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  font-size: 17px;
  margin-left: 15px;
}
.tab button:hover {
  fill: var(--red);
}
.tab button:hover svg,
.tab button:hover svg.icon {
  fill: var(--red);
}

.tab button.active {
  fill: var(--red);
}

.tabcontent {
  border-top: none;
}

.badge {
  border: 2px solid var(--white);
  margin: 15px;
  padding: 7px 15px;
  display: block;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.075em;
  color: var(--red);
  background: var(--light-gray);
  text-align: center;
  font-size: 14px !important;
}

.categories-list .views-row {
  background: var(--light-gray);
}

.tab {
  padding: 0px 20px 20px 20px;
}
.tab button.tablinks.active {
  color: var(--red);
}

button.collapsible,
button.upcoming {
  color: var(--red);
  background: var(--white);
  border-color: var(--white);
  padding: 20px !important;
  font-size: 14px !important;
  letter-spacing: 0.16em;
  font-weight: 900;
  float: right;
  width: fit-content;
}

#List h1,
#Grid h1 {
  color: white;
  font-size: 0;
}

.sidebar {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidebar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidebar a:hover {
  color: var(--light-gray);
}

.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #111;
  color: white;
  padding: 10px 15px;
  border: none;
}

.openbtn:hover {
  background-color: #444;
}

#main {
  transition: margin-left 0.5s;
  padding: 16px;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidebar {
    padding-top: 15px;
  }
  .sidebar a {
    font-size: 18px;
  }
}
.tabs-collection {
  margin: 0;
  list-style-type: none;
}
.tabs-collection::after, .tabs-collection::before {
  display: table;
  content: " ";
}
.tabs-collection [role=tablist] {
  margin: 0 0 -0.1rem;
  overflow: visible;
  font-size: 0;
}
.tabs-collection [role=tab] {
  position: relative;
  margin: 0;
  padding: 1rem 1rem 1.3rem;
  border: none;
  overflow: visible;
  font-family: var(--sans-serif);
  font-weight: var(--bold);
  background: none;
  transition: background 0.8s ease-out;
  cursor: pointer;
}
.tabs-collection [role=tab]::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 5px;
  content: "";
  transition: 0.3s;
  z-index: 1;
}
.tabs-collection [role=tab]::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 5px;
  content: "";
  transition: 0.3s;
}
.tabs-collection [role=tab]:focus-visible::before, .tabs-collection [role=tab]:hover::before {
  background: var(--gray);
  width: 100%;
}
[class*=bg--gold] .tabs-collection [role=tab]:focus-visible::before, [class*=bg--gold] .tabs-collection [role=tab]:hover::before {
  background: var(--gray);
}
.tabs-collection [role=tab]:active, .tabs-collection [role=tab]:focus, .tabs-collection [role=tab]:hover {
  outline: 0;
}
.tabs-collection [role=tabpanel] {
  position: relative;
  z-index: 2;
}
.tabs-collection [role=tabpanel].is-hidden {
  display: none;
}
.tabs-collection [role=tabpanel] * + p {
  margin-top: 1rem;
}

button#inactive-tab svg {
  color: #767676;
}

button#list-tab svg,
button#grid-tab svg {
  fill: var(--red);
}
button#list-tab svg:hover,
button#grid-tab svg:hover {
  fill: var(--red);
}

.toggle-list-class #List,
.toggle-grid-class #Grid,
#List .resource-card {
  display: none;
}

button#active-grid-icon-tab:hover svg,
button#active-list-icon-tab:hover svg {
  fill: var(--red);
}

.toggle-list-class button#active-grid-icon-tab svg {
  fill: var(--red);
}

.toggle-grid-class button#active-list-icon-tab svg {
  fill: var(--red);
}

#List .view-empty {
  display: none;
}

.list-class #List {
  display: none;
}

.grid-class #Grid {
  display: none;
}

.toggle-grid-class div#card-display {
  display: none;
}

